
import { defineComponent, ref, PropType } from 'vue'
import NavTerm from 'LLFetcher/src/types/navterm';
import Store from 'LLFetcher/src/libs/store';
const __sfc_main = defineComponent({
  props:{
    nav :{
        required:true,
        type: String as PropType<NavTerm>
    }
  },  
  setup(props) {
    const nav = ref(props.nav);

    return {nav};
  },
  methods:{
    onNavClick(target : NavTerm){
        console.log(target);
        this.nav = target;
        Store.setAppNav(this.nav);
        this.$emit('update', target);
    }
  }
})

          import { render } from "./components/PageNavigation.vue?vue&type=template"

          __sfc_main.render = render
          
export default __sfc_main